body {
  font-family: 'Monospace, sans-serif';
  background-color: #000;
  margin: 0;
  padding: 0;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  text-align: center;
}

main {
  padding: 20px;
}

.projects, .courses, .about, .home {
  margin-bottom: 40px;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.card {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 300px;
}

h2 {
  border-bottom: 2px solid #007acc;
  padding-bottom: 10px;
}

a {
  color: #007acc;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.nav-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #000;
}

.nav-link {
  background: #fff;
  color: #000;
  padding: 20px 40px;
  border-radius: 5px;
  text-decoration: none;
  font-weight: bold;
  margin: 10px;
}

.nav-link:hover {
  background: #ccc;
}

footer {
  text-align: center;
  padding: 20px;
  background-color: #282c34;
  color: white;
}